import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { SubTypeOfHousingComponent } from './sub-type-of-housing.component';

const routes: Routes = [
  {
    path: '',
    component: SubTypeOfHousingComponent,
    data: {
      title: 'Sub Type of Housing'
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SubTypeOfHousingRoutingModule {}



